import React, { lazy} from 'react';
import { FaBox, FaCertificate, FaList, FaPallet, FaTachometerAlt, FaUserCog, FaUsers } from "react-icons/fa";

const Dashboard = lazy(() => import('../pages/Dasboard'));
const Productos = lazy(() => import('../pages/Productos'));
const Operadores = lazy(() => import('../pages/Operadores'));
const Clientes = lazy(() => import('../pages/Clientes'));
const Categorias = lazy(() => import('../pages/Categorias'));
const Secciones = lazy(() => import('../pages/Secciones'));

const Marcas = lazy(() => import('../pages/Marcas'));

// const FichaAlmacen = lazy(() => import('../pages/Almacenes/FichaAlmacen'));
const FichaProductos = lazy(() => import('../pages/Productos/Ficha'));

const routesAdmin = () => [
  {
    title: 'dashboard',
    url: '/',
    icon: <FaTachometerAlt />,
    exact: true,
    component: Dashboard
  },
  {
    title: 'operadores',
    url: '/operadores',
    icon: <FaUserCog />,
    exact: true,
    component: Operadores
  },
  {
    title: 'clientes',
    url: '/clientes',
    icon: <FaUsers />,
    exact: true,
    component: Clientes
  },
  {
    title: 'secciones',
    url: '/secciones',
    icon: <FaCertificate />,
    exact: true,
    component: Secciones
  },
  {
    title: 'categorias',
    url: '/categorias',
    icon: <FaBox />,
    exact: true,
    component: Categorias
  },
  // {
  //   title: 'marcas',
  //   url: '/marcas',
  //   icon: <FaCertificate />,
  //   exact: true,
  //   component: Marcas
  // },
  {
    title: 'productos',
    url: '/productos',
    icon: <FaPallet />,
    exact: true,
    component: Productos
  },
  {
    title: 'Ficha Productos',
    url: '/productos/ficha',
    icon: <FaList />,
    exact: true,
    component: FichaProductos,
    noShow: true
  },
  {
    title: 'Ficha Productos',
    url: '/productos/ficha/:id',
    icon: <FaList />,
    exact: true,
    component: FichaProductos,
    noShow: true
  },
]


const routes = (idPerfil = null) => {
  
  return routesAdmin();
}

export default routes;