import { useDispatch, useSelector } from "react-redux";
import { Form, Input } from "semantic-ui-react";
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { updateOperador } from "../../../actions/operadorActions";

const FormularioUsuario = ({ formRef, setOpen }) => {

  const { operador = {} } = useSelector(state => state.operadores);
  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: Object.keys(operador).length ? {
      ...operador,
      nuevoPasswordRepeat: ''
    } : initialValues(),
    validationSchema: Yup.object(validationSchema()),
    onSubmit: async values => {
      const formData = new FormData();

      Object.keys(values).forEach(key => {
        formData.append(key, values[key]);
      })
      const res = await dispatch(updateOperador(values.id, formData));
      if(res) {
        setOpen(false);
      }
    }
  })


  return ( 
    <div className="FormularioUsuario">
      <Form onSubmit={formik.handleSubmit} ref={formRef}>
        <Form.Group>
          <Form.Field
            id='form-input-control-first-name'
            control={Input}
            label='Usuario'
            name="userName"
            value={formik.values.userName}
            onChange={formik.handleChange}
            width={6}
          />
          <Form.Field
            id='form-input-control-last-name'
            control={Input}
            label='Nueva Contraseña'
            type='password'
            name='password'
            value={formik.values.password}
            error={formik.errors.password}
            onChange={formik.handleChange}
            width={6}
          />
          <Form.Field
            id='form-input-control-last-name'
            control={Input}
            label='Repetir contraseña'
            type='password'
            name='nuevoPasswordRepeat'
            value={formik.values.nuevoPasswordRepeat}
            onChange={formik.handleChange}
            error={formik.errors.nuevoPasswordRepeat}
            width={6}
          />
        </Form.Group>
      </Form>
    </div>
  );
}

export default FormularioUsuario;

const initialValues = () => {
  return ({
    userName: '',
    password: '',
    nuevoPasswordRepeat: ''
  })
}

const validationSchema = () => {
  return {
    userName: Yup.string().required('Este campo es requerido.'),
    password: Yup.string().required('Este campo es requerido.').matches(
      "^[A-Za-z0-9]{8,12}$",
      "Ingrese como mínimo 8 caracteres."
    ),
    nuevoPasswordRepeat: Yup.string().required('Este campo es requerido.').oneOf([Yup.ref("password"), null], "Contraseñas no coinciden")   
  }
}
